body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


footer {
    text-align: center;
    background-color: rgb(0, 0, 0);
    color: azure;
    padding: 20px;
    border-top: 5px solid white;
}

.header {
    font-family: Rock Salt;
    font-size: 55px;
    text-align: center;
    margin-top: 2%;
    background-color: blanchedalmond;
}

.page_image {
    width: 60%;
    height: auto;
    justify-items: center;
    border-radius: 20%;
    border: 4px solid white;
    margin-left: 20%;
   
}

.credit {
    text-align: center;
    font-family: Pangolin;
    font-weight: bold;
}
.container {
    background-color:rgb(218, 210, 210);
    border: 5px black solid;
    margin-top: 5%;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
    font-family: Pangolin;
    padding: 1em;
    display: inline-block;
}

.link {
    font-weight: bold;
    font-family: Edu NS ACT Foundation;
    font-size: 2.3em;
    text-shadow: 2px 2px #0a0a0a;
    text-align: center;
}
h3 {
    font-weight: bold;
    font-family: Edu NS ACT Foundation;
    display: flex;
    flex-direction: row;
}

.placeimg {
    width: auto;
    height: 200px;
    align-items: center;
}
.placeimg2 {
    width: 20%;
    height: auto;

    align-items: center;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.edit {
    padding: 1em;
}

.formContainer {
    /* margin-top: 10%; */
    background-color:rgb(80, 185, 226);
    border: 5px black solid;
    margin-left: 15%;
    margin-right: 15%;
    font-family: Orbitron;
    padding: 1em;

}
input {
    width: 100%;
    margin-bottom: 3%;
}

textarea {
    width: 100%;
}

label {
    font-family: Pangolin;
    color: rgb(80, 185, 226);
    background-color:rgb(7, 7, 7);
    border: 1px black solid;
    margin-top: 7px;
    padding:0.1%;
}
h2 {
    margin-top: 5%;
    display: flex;
    justify-content: center;
    font-family: Pangolin;
}
h3 {

    margin-bottom: 2%;
    display: flex;
    justify-content: center;
    font-family: Pangolin;
}

.btn-secondary {
    background-color: black;
    
    
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
}
.navbar-brand {
    font-family: Rock Salt ;
    font-weight: bold;
}

.logo {
    display: block;
    position: relative;
    width: auto;
    height: 60px;
}

.nav-link {
    font-weight: bold;
    color: white;
    font-family: Allerta Stencil;
    text-align: center; 
}
